h1, h2 {
  margin: 0;
  padding: 0;
  text-align: center;
  display: block;
  line-height: 10vw;
}

h1 {
  font-weight: bold;
  padding-top: 0.75rem;
  margin-bottom: 2.25rem;
}

h2 {
  font-weight: normal;
}

h1 {
  font-size: 8vw;
}
h2 {
  font-size: 5vw;
}

number {
  font-weight: bold;
  font-size: 10vw;
}

/* Multiple properties with same values */
h1 {
  // @include fluid-type(padding-bottom padding-top, 20em, 70em, 2em, 4em);
}

@font-face {
  font-family: 'tesla-saves-lives';
  src:  url('#{$icomoon-font-path}/tesla-saves-lives.eot?ltstyk1');
  src:  url('#{$icomoon-font-path}/tesla-saves-lives.eot?ltstyk1#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/tesla-saves-lives.ttf?ltstyk1') format('truetype'),
    url('#{$icomoon-font-path}/tesla-saves-lives.woff?ltstyk1') format('woff'),
    url('#{$icomoon-font-path}/tesla-saves-lives.svg?ltstyk1#tesla-saves-lives') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="tsl-"], [class*=" tsl-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'tesla-saves-lives' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

